@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cookie&display=swap);
@import url(https://fonts.googleapis.com/css?family=Serendipity+Wide&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: Bluehonest;
  src: url("/static/media/Bluehonest Demo Version.f88b075f.otf");
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.top-nav {
  position: 'relative';
  display: 'float';
  justifyContent: 'center';
}

.main {
  margin-top: 300px;
}

.menu {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 99;
}

.ant-menu-root {
  display: inline-block;
  width: 326px;
}

.home {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: -20px;
}

.event{
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 40px;
  background-color: #bf9b9b;
}

.story {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 40px;
  background-color: #8bbbd9;
}

.rsvp {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 40px;
  background-color: #bf9b9b;
}

.s-heading h1 {
  color: var(--highlight);
  font-size: 3rem;
  margin: 0px;
  padding: 0px;
  font-family: 'Cookie', cursive;
  font-weight: 400!important;
}
.s-heading p {
  color: var(--highlight);
  font-size: 1rem;
  margin: 0px;
  padding: 0px;
}
.s-heading {
  text-align: center;
  margin: 20px 0px;
}

.name {
  font-family: 'Bluehonest', cursive;
  font-weight: 400!important;
  font-size: 3.6vw;
  position: absolute;
  top: 107px;
  letter-spacing: 1px;
}

.prename {
  position: absolute;
  top: 70px;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 1.5vw;
}

.imgContainer{
  position:relative;
  width: 100%;
  height: 100%;
  display: table;
  z-index: -1;
}

.imgContainer img {
  position: relative;
}

.desktop.imgContainer {display:block;}
.mobile.imgContainer {display:none}

@media all and (max-width: 640px) {
  .desktop.imgContainer {display: none;}
  .mobile.imgContainer {display: block;}
  .name {top: 90px;}
}
